
:root {
  --positive-color: '#ff5b09';
  --negative-color: '#09adff';
}
.hidden-cluster {
  stroke: black;
  stroke-opacity: 0.2;
  stroke-width: 1;
  fill: gray;
  fill-opacity: 0.1;
}
.hidden-cluster.active {
  stroke-width: 1.5;
  stroke-opacity: 0.7;
  stroke: black;
  fill-opacity: 0.1;
}
#middle_line {
  stroke: lightskyblue;
  stroke-width: 2;
  stroke-dasharray: 3, 3;
  fill: none;
  opacity: 0.5;
}
.link.active {
  opacity: 1;
}
.link {
  fill: none;
  opacity: 0.2;
}
.link.positive {
  stroke: #ff5b09;
}
.link.negative {
  stroke: #09adff;
}
.unit {
  stroke: 'none';
}
.unit-active {
  stroke: black;
  stroke-width: 1.0;
}
.wordcloud.active {
  stroke: 'black';
  stroke-width: 1.5;
  stroke-opacity: 0.7;
}
.axis path, .axis tick, .axis line {
  fill: none;
  stroke: none;
}




