
.links .active {
  stroke-width: 3;
}
.links {
  stroke-width: 1.5;
  pointer-events: none;
}
.states .active {
  stroke-width: 2.0;
  stroke: black;
  opacity: 1.0;
}
.states {
  stroke-width: 0.0;
  opacity: 0.7;
}
.words .active {
  stroke-width: 0.5;
  stroke: black;
  opacity: 1.0;
}
.words {
  font-size: 13;
  opacity: 0.8;
  stroke: black;
  stroke-width: 0;
}



