
#app {
  font-family: "Helvetica Neue",Helvetica,Arial,sans-serif;
  /*font-family: 'Avenir', Helvetica, Arial, sans-serif;*/
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  /*color: #2c3e50;*/
  margin-top: 10px;
}
.el-row {
  margin-bottom: 10px;
&:last-child {
    margin-bottom: 0;
}
}
.el-col {
  border-radius: 4px;
}
.bg-purple-dark {
  background: #abc;
}
.bg-purple {
  background: white;
}
.bg-purple-light {
  background: #fff;
}
.grid-content {
  border-radius: 4px;
  min-height: 36px;
}
.col-bg {
  padding: 5px 0;
  background-color: white;
}
.row-bg {
  padding: 10px 0;
  background-color: white;
}
.border {
  border-style: solid;
  border-width: 1px;
  border-color: #99A9BF;
}
.normal {
  font-weight: normal;
}
h4 {
  font-size: 14px;
  color: #555;
  line-height: 18px;
  margin-top: 0px;
  padding-left: 10px;
  padding-top: 5px;
  padding-bottom: 5px;
  margin-bottom: -10px;
  text-align: left;
  background-color: rgba(128, 128, 128, 0.1);
}
.header-menu {
  height: 60px;
  /*margin-bottom: -10px;*/
}
.logo {
  font-size: 18px;
}




